.carousel {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    height: 100%;

    .button {
        display: flex;
        flex-direction: column;
        width: 5%;
        justify-content: center;
        color: black;
        font-size: 4em;
        cursor: pointer;
    }
    .content {
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;

        .description {
            padding: 0.5em;
            display: flex;
            width: 40%;
            height: 100%;
        }

        .photo {
            padding: 0.5em;
            display: flex;
            width: 60%;
            height: 100%;
        }
    }
}